import React, { useRef } from 'react';
import CardFront from './CardFront';
import CardBack from './CardBack';
import { styled } from '@mui/system';

const Container = styled('div')({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  height: '100%',
  margin: '10px'
});

//     2  
const CardSection = ({ player, batter, pitcher }) => {
  
  if (player === null) {
    return null; //  null   
  }

  return (
    <Container>
        <CardFront id="front" en_name={ player[' ']} name = { player[''] } number = { player[''] } />
        <CardBack id="back" player = { player } data= { player['']==="P" ? pitcher : batter }/>
        {/* <CardBack pitcher = { player.pitcher } pitcher_add = { player.pitcher_add } comment = { player.comment2 } comment_style = { player.comment2_style }/> */}
    </Container>
  );
};

export default CardSection;